</template>

<script>
import Aplayer from 'vue-aplayer';
export default {
  name: 'UserPorfile',
  data() {
    return {
      playing: null,
      songList: [
        {
          title: '12313213',
          artist: '4444',
          src: 'https://firebasestorage.googleapis.com/v0/b/meason-music.appspot.com/o/music%2F%E6%88%91%E9%82%84%E5%B9%B4%E8%BC%95.mp3?alt=media&token=21d2d37e-012c-4f37-afcb-083024546670',
          pic: 'htt ps://moeplayer.b0.upaiyun.com/aplayer/secretbase.jpg',
          lrc: '[00:00.00]/partner',
          author: '霈霈豬',
          likes: [],
          type: '搖滾',
          uid: '454544'
        }
      ],
    }
  },
  components: {
    Aplayer
  },
  methods: {
    togglePlay(item) {
      const vm = this;
      let newSongList = [];
      vm.songList.forEach(i => {
        newSongList.push(i)
      });
      newSongList.unshift(vm.songList[item]);
      delete newSongList[item];
      this.$bus.$emit('audio:list', newSongList);
    }
  },
}
</script>

